import React, {Component} from 'react';
import styled from 'styled-components';
import {ScoreWordsWrapper, WordWrapper, SliderWrapper, SliderTrack, Score, Word} from './style';

const NoScore = () =>{
  return(
    <div>
    <p style ={{color: '#EB867C'}}>No Score Yet! </p>
    <p style ={{fontSize: '0.7em', color: '#EB867C', fontStyle: 'initial'}}>(click slider to save your score)</p>
    </div>
  )
}
const RatingTrack = (props) =>{
  let {
        trackImg,
        thumbImg,
      } = props;
  let ScoreWords = props.ScoreWords || [1,2,3,4,5];
  let WordWidth = 100.0/ScoreWords.length;
  let score = props.score === undefined? <NoScore /> : ScoreWords[props.score - 1] + '!';
  function onChangeHandler (score){
    console.log(score)
  }
    return(
      <SliderWrapper width={props.width || 100}>
        <ScoreWordsWrapper>
          {ScoreWords.map((word)=>{
            return(
              <WordWrapper width={WordWidth}>
                <Word>{word}</Word>
                <svg height={25} width={5}>
                  <line x1={0} y1={0} x2={0} y2={10} style={{stroke: 'rgb(142, 141, 138)', strokeWidth : 4}} />
                </svg>
              </WordWrapper>
            )
          })}
        </ScoreWordsWrapper>
        <SliderTrack
          type="range"
          min={props.min || 1}
          max={props.max || 5}
          step={props.step || 1}
          autofocus ={true}
          value = {score}
          trackImg = {trackImg}
          thumbImg = {thumbImg}
          style={{fontSize:"0.8em"}}
          onChange = {(event)=>onChangeHandler(event.target.value, props.title)}
        />
        <Score>
          {score}
        </Score>
      </SliderWrapper>
    )
};
export default RatingTrack;



