import {observable, computed} from 'mobx';
import {subjectsSelector} from '../../../helper/dataManipulators';
import axios from 'axios';
import {rawSubList, rawBoxList} from './rawData';

 const boxesStore = observable({
   subscriptionList: [],
   boxList : [],
   boxMap : {},
   productMap : {},
   boxIndex: 0,
   productIndex: 0,
   displaySubId: null,
   displayBoxId : null,
   displayProductId: null,

   flattenBoxesData(boxReviewData){
     this.boxList = boxReviewData.map(box => {
       const flattenedBox = {
         ...box,
         Products : box.Products.map((product) => {
           this.productMap[product.Barcode] = product;
           return product.Barcode;
         }),
       }
       this.boxMap[box.BoxID] = flattenedBox;
       return box.BoxID;
     })
   },

   getSubscriptionList() {
     this.subscriptionList = rawSubList;
     this.displaySubId = this.subscriptionList[0].id;
   },

   setBoxReviewData(boxReviewData){
     this.flattenBoxesData(boxReviewData);
     this.boxIndex = this.boxList.slice().length - 1;
     this.boxIndex = 0;
     this.displayBoxId = this.boxList[this.boxIndex];
     this.productIndex = 0;
     this.displayProductId = this.boxMap[this.displayBoxId].Products[this.productIndex];
   },

   initBoxReviewData() {
    const boxData = rawBoxList;
    this.setBoxReviewData(boxData);
    console.log(this.boxList.slice());
    console.log(this.boxMap);
    console.log(this.productMap);
    },

    setDisplayBoxId(newIndex) {
      if (newIndex < 0 || newIndex >= this.boxList.length) return;
      console.log(newIndex);
      this.boxIndex = newIndex;
      this.displayBoxId = this.boxList[this.boxIndex];
      this.displayProductId = this.boxMap[this.displayBoxId].Products[0];
      console.log(this);
    },

    setSubscriptionId(subId) {
        this.displaySubId = subId;
        console.log(this)
    },

    selectBox(boxId){
      console.log(boxId);
      return {
        ...this.boxMap[boxId],
        Products: subjectsSelector(this.boxMap[boxId].Products, this.productMap),
      }
    },

    setDisplayProductId(newIndex){
      console.log(this);
      const curBox = this.boxMap[this.displayBoxId].Products;
      if (newIndex < 0 || newIndex >= curBox.length) return;
      this.productIndex = newIndex;
      this.displayProductId = curBox[this.productIndex];
      console.log(this);
    },

    setRating(FitRating, StyleRating){
      this.productMap[this.displayProductId].FitRating = FitRating;
      this.productMap[this.displayProductId].StyleRating = StyleRating;
      axios.post('/user', {
        BoxID: this.boxMap[this.displayBoxId].BoxID,
        ProductID: this.displayProductId,
        FitRating: FitRating,
        StyleRating: StyleRating,
      })
      .then(function (response) {
        if(response.status === 200){
          alert("Rating updated! Thank you!");
        } else {
          alert("Update Rating Failed");
        }
      })
      .catch(function (error) {
        alert("Update Rating Failed");
        console.log(error);
      });
    }
  })

export default boxesStore;



